// src/utils/api.js
import axios from 'axios';

const api = axios.create({
  baseURL: 'http://43.202.79.178/', //  
});

//    Authorization   
api.interceptors.request.use(
  (config) => {
    // config Authorization  
    return config;
  },
  (error) => {
    return Promise.reject(error);
  }
);

export default api;
